import React, {
  useState,
  useEffect,
  useRef,
  useCallback,
  CSSProperties,
} from "react";
import { getsbclient } from "../../utils/sbclientinterface";
import { RealtimePostgresChangesPayload } from "@supabase/supabase-js";
const sbclient = getsbclient();

interface BroadcastRow {
  id: number;
  content: string | null; // Adjust the type of 'content'
  scroll_position: number | null;
}

interface ReadingTogetherProps {
  broadcastId: number;
}
const ReadingTogether: React.FC<ReadingTogetherProps> = ({ broadcastId }) => {

  const [broadcastedContent, setBroadcastedContent] = useState<string | null>(
    null
  );
  const contentRef = useRef<HTMLDivElement>(null);
  const observerRef = useRef<IntersectionObserver | null>(null);
  const visibleElements = useRef<Set<Element>>(new Set()); 
  const topElementIdRef = useRef<string | null>(null);
  const [isBroadcastingScroll, setIsBroadcastingScroll] = useState(false);
  const [isLeader, setIsLeader] = useState(false);
  const [shouldSyncScroll, setShouldSyncScroll] = useState(false);
  const [shouldSyncContent, setShouldSyncContent] = useState(true);


// TypeScript version of debounce
function debounce<T extends (...args: any[]) => any>(
  func: T,
  delay: number
): (...args: Parameters<T>) => void {
  let timeoutId: NodeJS.Timeout | null = null;

  return function (this: ThisParameterType<T>, ...args: Parameters<T>): void {
    if (timeoutId) {
      clearTimeout(timeoutId);
    }
    timeoutId = setTimeout(() => {
      func.apply(this, args);
    }, delay);
  };
}
  const debouncedSendForScrollSync = useCallback(debounce((rowId: string) => {
 
    /*
    console.log(`inside debouncedSendForScrollSync with rowId as ${rowId}`);
    console.log(`broadcastId is ${broadcastId}`)
    console.log(`isLeader ${isLeader}`)
    console.log(`isBroadcastingScroll is ${isBroadcastingScroll}`)
    */
    if (broadcastId && isLeader&& isBroadcastingScroll  ) {
      //console.log(`broadcasting top visible row Id as ${rowId}`);
      sbclient
        .from('sankeertan')
        .update({ scroll_position: rowId })
        .eq('id', broadcastId)
        .then(() => {}
      );
    }
  }, 200), 
  [broadcastId, isLeader, isBroadcastingScroll, sbclient] // Add isLeader and isBroadcastingScroll here! Also ensure sbclient is there if used.
);

  

 
 
  const handleIsLeaderChange = (checked: boolean) => {
    setIsLeader(checked);
    if (checked) {
      setShouldSyncScroll(false);
    }
  };
  
  const handleIsBroadcastingScrollChange = (checked: boolean) => {
    setIsBroadcastingScroll(checked);
    if (checked) {
      setShouldSyncScroll(false);
    }
  };
  
  const handleShouldSyncScrollChange = (checked: boolean) => {
    setShouldSyncScroll(checked);
    if (checked) {
      setIsLeader(false);
      setIsBroadcastingScroll(false);
    }
  };




  const observeRows = useCallback(() => {
    //console.log('inside observeRows');
    const sections = contentRef.current?.querySelectorAll("tr[id]");
    const options = {
      root: contentRef.current,
      rootMargin: "0px",
      threshold: 0.5,
    };
  
    if (!sections) return;
  
    const callback: IntersectionObserverCallback = (entries, observer) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          visibleElements.current.add(entry.target);
        } else {
          visibleElements.current.delete(entry.target);
        }
      });
  
      let newTopElementId: string | null = null;
      let newTopElementPosition = Infinity;
  
      visibleElements.current.forEach((element) => {
        const rect = element.getBoundingClientRect();
        const rootRect = contentRef.current?.getBoundingClientRect();
        if (rootRect && rect.top >= rootRect.top && rect.top < newTopElementPosition) {
          newTopElementPosition = rect.top;
          newTopElementId = element.id;
        }
      });
  
      if (newTopElementId && newTopElementId !== topElementIdRef.current) {
        topElementIdRef.current = newTopElementId;
        const numericId = (newTopElementId as string).replace("trRowAsAnchor-", "");
        //console.log(`sending ${numericId} to debounceSendForScrollSync`);
        debouncedSendForScrollSync(numericId);
      }
    };
  
    observerRef.current = new IntersectionObserver(callback, options);
    sections.forEach((section) => {
      observerRef.current?.observe(section);
    });
  
    return () => {
      observerRef.current?.disconnect();
    };
  }, [debouncedSendForScrollSync ,contentRef ]);

  /*
  const broadcastScrollPosition = useCallback(
    debounce((scrollPos: number) => {
      if (isLeader && broadcastId && isBroadcastingScroll && false) {
        console.log(`need to update scrollposition as ${scrollPos}`)
        sbclient
          .from("sankeertan")
          .update({ scroll_position: scrollPos })
          .eq("id", broadcastId)
          .then(() => console.log("Broadcasted scroll position:", scrollPos));
      }
    }, 200),
    [broadcastId, isLeader, isBroadcastingScroll]
  );

  const handleScroll = useCallback(() => {
    //console.log("Checking if this method is being reached");
    if (contentRef.current) {
      broadcastScrollPosition(contentRef.current.scrollTop);
    }
  }, [broadcastScrollPosition]); // Depend on any values from the scope that handleScroll uses
  useEffect(() => {
    const contentNode = contentRef.current;
    if (contentNode) {
      contentNode.addEventListener('scroll', handleScroll);
    }
  
    return () => {
      if (contentNode) {
        contentNode.removeEventListener('scroll', handleScroll);
      }
    };
  }, [contentRef, handleScroll]); // Now handleScroll's identity is stable
  */
  useEffect(() => {
    //console.log("ReadingTogether Component Mounted");
    async function fetchInitialData() {
      const { data, error } = await sbclient
        .from("sankeertan")
        .select("*")
        .eq("id", broadcastId)
        .single<Pick<BroadcastRow, "content" | "scroll_position">>();

      if (error) {
        console.error("Error fetching broadcast data:", error);
        return;
      }
      setBroadcastedContent(data?.content);
      if (contentRef.current && data?.scroll_position !== null) {
        const rowElement = contentRef.current.querySelector(`#trRowAsAnchor-${data.scroll_position}`);
        if (rowElement) {
          rowElement.scrollIntoView({ behavior: "smooth",block: 'start' });
        }
      }
      
      /*
      setTimeout(() => {
      // Observe rows after initial content is loaded and contentRef is available
      if (contentRef.current) {
        //console.log("observing rows")
        observeRows();
      }    
    }, 500); // Adjust the delay as needed  
    */
    }

    fetchInitialData();

    const channel = sbclient.channel(`broadcast:${broadcastId}`);

    channel
      .on(
        "postgres_changes",
        {
          event: "UPDATE",
          schema: "public",
          table: "sankeertan",
          filter: `id=eq.${broadcastId}`,
        },
        (payload: RealtimePostgresChangesPayload<BroadcastRow>) => {
            if (payload.new && 'content' in payload.new && shouldSyncContent) {
            setBroadcastedContent(payload.new.content);
          }
          if (
            payload.new &&
            'scroll_position' in payload.new &&
            payload.new.scroll_position !== null && // Add null check if scrollPosition can be null
            contentRef.current &&
            shouldSyncScroll
          )  {
            const rowIdFromLeader = payload.new.scroll_position;
            const targetElement = contentRef.current.querySelector(
              `#trRowAsAnchor-${rowIdFromLeader}`
            );
            if (targetElement) {
              targetElement.scrollIntoView({ block: "start" });
            }
          }
        }
      )
      .subscribe();

    
    return () => {
      observerRef.current?.disconnect();
    channel.unsubscribe();
    };
  }, [broadcastId, shouldSyncContent, shouldSyncScroll]); 

  useEffect(() => {
    //console.log("useEffect for observeRows ran")
    //console.log("isBroadcastingScroll:", isBroadcastingScroll)
    //console.log("contentRef.current:", contentRef.current)
    //console.log("broadcastedContent:", broadcastedContent)

    if (isBroadcastingScroll && contentRef.current && broadcastedContent) {
      // Disconnect previous observer
      if (observerRef.current) {
        observerRef.current.disconnect();
        observerRef.current = null;
        visibleElements.current.clear();
        topElementIdRef.current = null;
      }
      //console.log("observeRows() called");
         // Introduce setTimeout before calling observeRows
    setTimeout(() => {
      if (contentRef.current) {
        observeRows();
        // Optional: Initial broadcast logic (if you had it)
      }
    }, 300); // Adjust the delay (in milliseconds) as needed

      // Optional: Initial broadcast logic
    } else if (!isBroadcastingScroll && observerRef.current) {
      // Disconnect observer when broadcasting stops
      observerRef.current.disconnect();
      observerRef.current = null;
      visibleElements.current.clear();
      topElementIdRef.current = null;
    }
    return () => {
      // Cleanup on unmount
      observerRef.current?.disconnect();
    };
  }, [isBroadcastingScroll, contentRef, broadcastedContent, observeRows, debouncedSendForScrollSync]);
  /*
  useEffect(() => {
    const contentNode = contentRef.current;
    if (contentNode) {
      contentNode.addEventListener('scroll', handleScroll);
    }
  
    return () => {
      if (contentNode) {
        contentNode.removeEventListener('scroll', handleScroll);
      }
    };
  }, [contentRef, handleScroll]); // Re-run if contentRef or handleScroll changes
  */
  const contentStyle: CSSProperties = {
    overflowY: "auto",
    height: "90dvh", // Adjust as needed
    scrollBehavior: 'smooth', // Add this line
  };

  if (!broadcastedContent) {
    return <div>Loading shared reading...</div>;
  }

  return (
    <div>
<div className="flex items-center space-x-4 mb-4">
<div className="flex items-center">
  <input
    type="checkbox"
    id="isLeader"
    className="form-checkbox h-5 w-5 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
    checked={isLeader}
    onChange={(e) => handleIsLeaderChange(e.target.checked)}
  />
  <label htmlFor="isLeader" className="ml-2 block text-gray-900 text-sm">
    Leader
  </label>
</div>

<div className="flex items-center">
  <input
    type="checkbox"
    id="broadcastScroll"
    className="form-checkbox h-5 w-5 text-green-600 focus:ring-green-500 border-gray-300 rounded"
    checked={isBroadcastingScroll}
    onChange={(e) => handleIsBroadcastingScrollChange(e.target.checked)}
    disabled={!isLeader}
  />
  <label htmlFor="broadcastScroll" className="ml-2 block text-gray-900 text-sm">
    Broadcast Scroll
  </label>
</div>

<div className="flex items-center">
  <input
    type="checkbox"
    id="followSyncScroll"
    className="form-checkbox h-5 w-5 text-purple-600 focus:ring-purple-500 border-gray-300 rounded"
    checked={shouldSyncScroll}
    onChange={(e) => handleShouldSyncScrollChange(e.target.checked)}
  />
  <label htmlFor="followSyncScroll" className="ml-2 block text-gray-900 text-sm">
    Follow Scroll
  </label>
</div>

<div className="flex items-center">
  <input
    type="checkbox"
    id="syncContent"
    className="form-checkbox h-5 w-5 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
    checked={shouldSyncContent}
    onChange={(e) => setShouldSyncContent(e.target.checked)}
  />
  <label htmlFor="syncContent" className="ml-2 block text-gray-900 text-sm">
    Sync Content
  </label>
</div>
</div>     
      <div
        ref={contentRef}
        style={contentStyle}
        className={`
          ${
            isLeader
            ? isBroadcastingScroll
              ? 'bg-green-200' // 3. isLeader and broadcasting scrolling - Light Green
              : 'bg-blue-200'   // 2. isLeader, not broadcasting scrolling yet - Light Blue
            : shouldSyncScroll
              ? 'bg-purple-200' // 4. isFollower & shouldSyncScroll - Light Purple
              : 'bg-gray-200'    // 1. Page is different, can be leader/follower - Light Gray (Default)
          }
          overflow-y-auto
          h-[80vh]
          p-4 // Add some padding for better visual separation
        `}
        dangerouslySetInnerHTML={{ __html: broadcastedContent || "" }}
      />
    </div>
  );
};

export default ReadingTogether;
